import React, { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import { db } from '../components/firebase';


const Details = () => {

const [ info, setInfo ] = useState([])   


// const { id } = useParams();

    //get data by id

   useEffect(() => {
        db.collection('users').onSnapshot(snapshot => {
            setInfo(snapshot.docs.map(doc=>doc.data()))
        })
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])


    return ( 
        <div>
          <h1 className="header">Registered Students</h1>
          <div className="container-fluid mt-4">
          <div className="card">
              <div className="card-header">Data</div>
              <div className="card-body">
                  <h5 className="card-title">Registration Confirmation</h5>
                  <p className="card-text">
                      {
                          info.map(
                            (student)=>(
                                <div>
                                    {student.name}, {student.surname} is a registered student at 
			                        Mlab CodeTribe 2021 and is of age {student.age}, from {student.location}.
                                </div>
                            )
                          )
                      }
                  </p>
                  <Link to="/Details/" className="btn btn-primary">
                      Home Page
                  </Link>
              </div>
          </div> 
          </div>
        </div> 
      );
}

export default Details