import React from 'react'
import '../Css/feed.css';

const Newsfeed = ({ title, description, url, urlToImage }) => {
    return (
        <div className="news-item">
            <img className="news-img" src={urlToImage} alt=""/>
            <h3>
                <a href="url">{title}</a>
            </h3>
            <p>{ description }</p>
        </div>
    )
}

export default Newsfeed
