import React, { Component } from 'react'
import {Navbar,Nav,Form,FormControl,Button} from 'react-bootstrap';
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";
import Forms from './Forms'; 
import Details from './Details';
import NewsList from './NewsList';

export default class NavbarComp extends Component {
    render() {
        return (
            <Router>
            <div>
                <Navbar bg="dark" variant="dark" expand="lg">
                <Navbar.Brand href="#home">Student-application</Navbar.Brand>
                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                <Navbar.Collapse id="basic-navbar-nav">
                    <Nav className="mr-auto">
                    <Nav.Link as={Link} to={"/form"}>Home</Nav.Link>
                    <Nav.Link as={Link} to={"/details"}>Details</Nav.Link>
                    <Nav.Link as={Link} to={"/NewsList"}>NewsList</Nav.Link>

                    {/* <NavDropdown title="Users" id="basic-nav-dropdown">
                        <NavDropdown.Item href="#action/3.1">L.s Sekgoka</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.2">MD Mawasha</NavDropdown.Item>
                        <NavDropdown.Item href="#action/3.3">T.D Sebola</NavDropdown.Item>
                        <NavDropdown.Divider />
                        <NavDropdown.Item href="#action/3.4">UserInfo</NavDropdown.Item>
                    </NavDropdown> */}

                    </Nav>
                    <Form inline>
                    <FormControl type="text" placeholder="Search" className="mr-sm-2" />
                    <Button variant="outline-success">Search</Button>
                    </Form>
                </Navbar.Collapse>
                </Navbar>
            </div>
            <div>
                <Switch>   
                <Route path="/NewsList">
                <NewsList/>
                </Route>
                <Route path="/Details/:id">
                <Details />
                </Route>
                <Route path="/">
                <Forms/>
                </Route>
                </Switch>
            </div>
            </Router>
        )
    }
}
